@include tablet{
    .contenedor-productos{
        @include grid(3,2rem);
    }
}
.producto{
    border-radius: 5%;
    background-color: lighten($gris,5);
    border: 1px solid darken($gris,10);
    margin-bottom: 2rem;

    .contenido-producto{
        padding: 2rem;
    }

    h3, p{
        margin: 0;
    }
    span{
        font-family: $fuente_nav;
        text-transform: uppercase;
    }
}
.precio{
    color: $grisOscuro;
    font-weight: bold;
    font-size: 3rem;
}
