.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet{
        align-items: flex-end;
    }

}


.barra{
    display: flex;
    flex-direction: column;
    align-items: center;
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
    }
}

.navegacion{
    visibility: hidden;
    opacity: 0;
    height: 0;
    transition: visibility .3s ease-in-out, opacity .3s linear, height .3s linear;
    @include tablet{
        display: flex;
        align-items: center;
        opacity: 1;
        visibility: visible;
    }
    a{
        font-family: $fuente_nav;
        color: $gris;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        text-transform: uppercase;
        
        &:hover{
            color: $deepPurple;
            transition: .3s;
        }

        @include tablet{
            margin-right:2rem ;
            font-size: 1.8rem;

            &:last-of-type{
                margin-right: 0;
            }
        }

    }
}

/*Burger Menu*/
.mobile-menu{
    @include tablet{
        display: none;
    }
    img{
        width: 5rem;

    }
    &:hover{
        cursor: pointer;
    }
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
}
