.contenedor-slider {
    position: absolute;
    width: 100%;
    height: 100%;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
    overflow: hidden;
  }
  
  
  .slides {
    position: relative;
    width: 100%;
    height: 100%;
    img {
        position: absolute;
        width: 50px;
        height: 50px;
        object-fit: cover;
        object-position: center;
        opacity: 0;
        transform: scale(0);
        transition: all 0.5s ease-in-out;
        transition-delay: 500ms;
      }>[data-active] {
        opacity: 1;
        transform: scale(1);
        transition-delay: 0ms;
        width: 100%;
        height: 100%;
        z-index: 10;
      }
  }
  .btn-slide {
    position: absolute;
    background: $gris;
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 50%;
    border: 1px solid rgba(34, 34, 34, 0.287);
    transition: opacity 300ms ease-in-out;
    cursor: pointer;
    overflow: hidden;
    z-index: 10;

    @include tablet{
        opacity: 0;
    }
  }
  
  .contenedor-slider:hover > .btn-slide {
    opacity: 1;
  }

  
  
  /* Previous and Next Button Position Absolute */
  .prev,
  .next {
    top: 50%;
    transform: translateY(-60%);
  }
  
  .prev {
    left: 20px;
  }
  .next {
    right: 20px;
  }
  
  /* Bottom Dots Container  */
  .container-dots {
    position: absolute;
    bottom: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    z-index: 10;
  }
  
  /* Sigle Dot style */
  .dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    border: 3px solid #f1f1f1;
    margin: 0 5px;
    background: #f1f1f1;
    cursor: pointer;
    transition: background-color 500ms ease-in-out;
  }
  
  /* Change the color of bg when user hover on it. */
  .dot:hover {
    opacity: 0.9;
    background: rgb(32, 32, 32);
  }
  
  /* Current or active dot */
  .dot[data-active="true"] {
    background: rgb(32, 32, 32);
  }
  