  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 1rem;
  width: 100%;
}
.boton-dorado-block:hover {
  background-color: #f0c644;
  cursor: pointer;
}

/** UI **/
.header {
  background-color: #1E1E1E;
  padding: 1rem 0 3rem 0;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.3s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  font-family: "Julius Sans One", sans-serif;
  color: #D9D9D9;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  text-transform: uppercase;
}
.navegacion a:hover {
  color: #1D267D;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

/*Burger Menu*/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}
.mobile-menu:hover {
  cursor: pointer;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.footer {
  padding: 2.5rem 0;
  background-color: #1E1E1E;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .contenedor-productos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.producto {
  border-radius: 5%;
  background-color: #e6e6e6;
  border: 1px solid silver;
  margin-bottom: 2rem;
}
.producto .contenido-producto {
  padding: 2rem;
}
.producto h3, .producto p {
  margin: 0;
}
.producto span {
  font-family: "Julius Sans One", sans-serif;
  text-transform: uppercase;
}

.precio {
  color: #333333;
  font-weight: bold;
  font-size: 3rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-extra {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-extra {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-extra:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #E08709;
}

.texto-entrada a {
  color: #1E1E1E;
  margin-top: 2rem;
  display: block;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-top: 1rem;
}

.imagen {
  display: flex;
  justify-content: center;
  margin-bottom: 2.5rem;
}

@media (min-width: 768px) {
  .contenedor-colecciones {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.coleccion {
  background-color: #f8f8f8 a;
}

/** Internas **/
.webp .banner {
  background-image: url(../img/.webp);
  height: 50rem;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  margin-bottom: 2rem;
}

.no-webp .banner {
  background-image: url(../img/.jpg);
  height: 50rem;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  margin-bottom: 2rem;
}

.contenido-banner {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.2); /*Sintaxis para añadir transparencias*/
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contenido-banner h2, .contenido-banner p {
  color: #FFFFFF;
  font-family: "Lato", sans-serif;
}

.contenedor-slider {
  position: absolute;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.slides {
  position: relative;
  width: 100%;
  height: 100%;
}
.slides img {
  position: absolute;
  width: 50px;
  height: 50px;
  object-fit: cover;
  object-position: center;
  opacity: 0;
  transform: scale(0);
  transition: all 0.5s ease-in-out;
  transition-delay: 500ms;
}
.slides > [data-active] {
  opacity: 1;
  transform: scale(1);
  transition-delay: 0ms;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.btn-slide {
  position: absolute;
  background: #D9D9D9;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 50%;
  border: 1px solid rgba(34, 34, 34, 0.287);
  transition: opacity 300ms ease-in-out;
  cursor: pointer;
  overflow: hidden;
  z-index: 10;
}
@media (min-width: 768px) {
  .btn-slide {
    opacity: 0;
  }
}

.contenedor-slider:hover > .btn-slide {
  opacity: 1;
}

/* Previous and Next Button Position Absolute */
.prev,
.next {
  top: 50%;
  transform: translateY(-60%);
}

.prev {
  left: 20px;
}

.next {
  right: 20px;
}

/* Bottom Dots Container  */
.container-dots {
  position: absolute;
  bottom: 20px;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 10;
}

/* Sigle Dot style */
.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 3px solid #f1f1f1;
  margin: 0 5px;
  background: #f1f1f1;
  cursor: pointer;
  transition: background-color 500ms ease-in-out;
}

/* Change the color of bg when user hover on it. */
.dot:hover {
  opacity: 0.9;
  background: rgb(32, 32, 32);
}

/* Current or active dot */
.dot[data-active=true] {
  background: rgb(32, 32, 32);
}