.webp .banner{
    background-image: url(../img/.webp);
    height: 50rem;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    margin-bottom: 2rem;
    }

.no-webp .banner{
    background-image: url(../img/.jpg);
    height: 50rem;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    margin-bottom: 2rem;
}
.contenido-banner{
    position: absolute;
    background-color: rgba(0, 0, 0, 0.2); /*Sintaxis para añadir transparencias*/
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h2,p{
        color: $blanco;
        font-family: $fuente_principal;
    }

}